import './App.css';

import { BrowserRouter, Route, Switch, useLocation } from 'react-router-dom';
import { CSSTransition, TransitionGroup } from 'react-transition-group';

import About from './pages/About';
import Analytics from './components/Template/Analytics';
import Contact from './pages/Contact';
import { CssBaseline } from '@material-ui/core';
import { HelmetProvider } from 'react-helmet-async';
import Home from './pages/Home';
import Main from './layout/Main';
import NotFound from './pages/NotFound';
import { PageContentLayoutPlaceholder } from './layout/PageContentLayout'; // fallback for lazy pages
import Projects from './pages/Projects';
import ProvideAppTheme from './context/AppThemeContext';
import React from 'react'; // , { Suspense, lazy }
import Resume from './pages/Resume';
import Stats from './pages/Stats';
import { StyledEngineProvider } from '@material-ui/core/styles';

const { PUBLIC_URL } = process.env;

// const Home from './pages/Home'));
// const About from './pages/About'));
// const Resume from './pages/Resume'));
// const Projects from './pages/Projects'));
// const Stats from './pages/Stats'));
// const Contact from './pages/Contact'));
// const NotFound from './pages/NotFound'));

const AppModule = () => {
  const location = useLocation();
  return (
    <>
      <HelmetProvider>
        <Analytics />
        <Main>
          <TransitionGroup>
            <CSSTransition
              timeout={300}
              classNames="route-nav"
              key={location.pathname}>
              {/* <Suspense fallback={<PageContentLayoutPlaceholder />}> */}
              <Switch location={location}>
                <Route exact path="/" component={Home} />
                <Route path="/about" component={About} />
                <Route path="/resume" component={Resume} />
                <Route path="/projects" component={Projects} />
                <Route path="/stats" component={Stats} />
                <Route path="/contact" component={Contact} />
                <Route path="/plhl" component={PageContentLayoutPlaceholder} />
                <Route path="*" component={NotFound} />
              </Switch>
              {/* </Suspense> */}
            </CSSTransition>
          </TransitionGroup>
        </Main>
      </HelmetProvider>
    </>
  );
};

const App = () => {
  return (
    <>
      <ProvideAppTheme>
        <CssBaseline>
          <StyledEngineProvider injectFirst>
            <BrowserRouter basename={PUBLIC_URL}>
              <AppModule />
            </BrowserRouter>
          </StyledEngineProvider>
        </CssBaseline>
      </ProvideAppTheme>
    </>
  );
};

export default App;
